<template>
  <div align="center">
    <br/>
    <mu-avatar slot="center" icon="person" :size="100"></mu-avatar>
    <br/>
    <mu-text-field disabled="true" label="" v-model="currentUser._id" labelFloat/>
    <br/>
    <mu-text-field disabled="true" label="" v-model="currentUser.name" labelFloat/>
    <br/>
    <mu-text-field disabled="true" label="" v-model="currentUser.roleName" labelFloat/>
    <br/>
    <div>
      <!--<mu-raised-button style="display:inline-block" size="normal" label="" @click.native="handleChgPass" primary/>-->
      <mu-raised-button style="display:inline-block" size="normal" label="" @click.native="handleLogout"/>
    </div>
    <mu-dialog :open="askLogout" title="" @close="closeLogoutDialog">
      
      <mu-flat-button slot="actions" @click="closeLogoutDialog" primary label=""/>
      <mu-flat-button slot="actions" primary @click="logout" label=""/>
    </mu-dialog>
  </div>
</template>

<script>
  import { mapGetters, mapActions } from 'vuex'
  import { USER_LOGOUT, CHANGE_APP_TITLE } from '../../store/mutation_types'
  import ObjUtil from '../../utils/ObjUtil'
  import Util from '../../store/utils'

  export default {
    name: 'app',
    components: {
    },
    computed: {
      ...mapGetters(['user']),
      currentUser () {
        var data = {}
        data = ObjUtil.clone(this.user)
        data.roleName = Util.getRoleNames(this.user.role)
        return data
      }
    },
    data: () => {
      return {
        askLogout: false
      }
    },
    beforeRouteEnter: function (to, from, next) {
      next(vm => { vm.CHANGE_APP_TITLE('') })
    },
    methods: {
      handleLogout () {
        this.askLogout = true
      },
      closeLogoutDialog () {
        this.askLogout = false
      },
      logout () {
        this.askLogout = false
        this.USER_LOGOUT()
      },
      handleChgPass () {

      },
      ...mapActions([CHANGE_APP_TITLE, USER_LOGOUT])
    }
  }
</script>
<style scoped>

</style>



