<template>
  <div>
    <!--<mu-select-field v-model="queryUser" label="" hintText="">-->
      <!--<mu-menu-item v-for="user in allUser" :value="user._id" :title="user.name"/>-->
    <!--</mu-select-field>-->
    <mu-date-picker label="" hintText="" v-model="startQueryDate"/>
    <br/>
    <mu-date-picker label="" hintText="" v-model="endQueryDate"/>
    <br/>
    <mu-raised-button style="margin-top: 10px" label="" @click="getData"></mu-raised-button>
    <br/>
    <br/>
    <mu-divider shallowInset/>
    <mu-list @itemClick="itemClicked">
      <mu-list-item  v-for="item in all_statistic_data" :title="item.username" :describeText="item.role" :value="item" :afterText="': ' + item.unfinish_count">
        <mu-avatar icon="person" slot="leftAvatar"/>
        <mu-icon value="chevron_right" slot="right"/>
      </mu-list-item>
    </mu-list>
  </div>
</template>
<style>
  .el-table .info-row {
    background: #c9e5f5;
  }

  .el-table .positive-row {
    background: #e2f0e4;
  }
</style>
<script>
  import { mapActions, mapGetters } from 'vuex'
  import { CHANGE_APP_TITLE, GET_ALL_USER_TASK_EXEC_DATA, GET_ALL_USER_TASK_EXEC_DATA_BY_DATERANGE, GET_USER_TASK_EXEC_DATA_BY_DATERANGE } from '../../store/mutation_types'
  import dateUtil from '../../utils/DateUtil'
  import Moment from 'moment'

  export default {
    name: 'table_manage_unfinished_data',
    methods: {
      tableRowClassName (row, index) {
        let item = row
        if (item.unfinish_count > 0) {
          return 'info-row'
        }
        return ''
      },
      handleStartDateChange (value) {
        console.log(value)
        console.log(Moment().format('YYYY-MM-DD'))
      },
      itemClicked (item) {
        this.selectedData = {}
        this.selectedData.userid = item.value.userid
        this.selectedData.startofday = dateUtil.getDatetimeSeconds(this.startQueryDate)
        this.selectedData.endofday = dateUtil.getDatetimeSeconds(this.endQueryDate)
        this.$router.push({name: 'oneUserAllTaskStat', params: {selectedData: this.selectedData}})
      },
      getData () {
        var param = {}
        param.starttime = dateUtil.getDatetimeSeconds(this.startQueryDate)
        param.endtime = dateUtil.getDatetimeSeconds(this.endQueryDate)
        this.GET_ALL_USER_TASK_EXEC_DATA_BY_DATERANGE(param)
      },
      showEditOver () {
        this.showEdit = false
      },
      ...mapActions([CHANGE_APP_TITLE, GET_ALL_USER_TASK_EXEC_DATA, GET_ALL_USER_TASK_EXEC_DATA_BY_DATERANGE, GET_USER_TASK_EXEC_DATA_BY_DATERANGE])
    },
    computed: {
      ...mapGetters(['all_statistic_data', 'allUser', 'user'])
    },
    mounted: function () {
      this.getData()
    },
    beforeRouteEnter: function (to, from, next) {
      next(vm => { vm.CHANGE_APP_TITLE('') })
    },
    props: ['datePickerOption'],
    data: () => {
      return {
        showEdit: false,
        selectedData: {},
        selectedDay: new Date(),
        startQueryDate: Moment(dateUtil.getStartofThisMonth() * 1000).format('YYYY-MM-DD'),
        endQueryDate: Moment().format('YYYY-MM-DD'),
        queryUser: this.user,
        datetime_type: 'month'
      }
    },
    components: {
    }
  }
</script>



